import React from 'react';

function Product({ hit }) {
    return (
      <a
        className="product"
        href={hit.url}
        target="_blank"
        rel="noopener noreferrer"
      >
        <img src={hit.image} alt={hit.name} />
        <div>
          <h3>{hit.brand}</h3>
          <h2>{hit.name}</h2>
          <p>${hit.price}</p>
        </div>
      </a>
    );
  }

  export default Product;